.vds-chip-container {
  .vds-chip-input {
    flex: 1;
    height: 28px;
    min-width: 300px;
    border: 0;
    font-size: 14px;
    height: 28px;

    &:focus {
      outline: 0;
    }
  }

  &.vds-chip-type-input {
    align-items: flex-start;
    display: flex;
    flex-wrap: wrap;
    padding: .5rem;
    border: 1px solid #616188;

    &.vds-state--focus {
      border-color: #0051dc;
      border-bottom-width: 2px;
    }
  }
}
